"use client";

import { useState } from "react";

export default function UploadPage() {
  const [file, setFile] = useState<File | null>(null);
  const [status, setStatus] = useState("");

  const handleUpload = async () => {
    if (!file) {
      setStatus("Please select a file first.");
      return;
    }

    const formData = new FormData();
    formData.append("file", file);

    // You can add division/week/team fields if needed:
    // formData.append("division", "1");

    const res = await fetch("/api/upload-replay", {
      method: "POST",
      body: formData,
    });

    const data = await res.json();
    setStatus(data.message);
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gray-800 p-6">
      <div className="bg-gray-500 rounded-xl shadow-lg p-10 w-full max-w-md text-center">
        <h1 className="text-3xl font-bold mb-6 text-gray-800">Upload Replay</h1>

        {/* File input button */}
        <input
          type="file"
          accept=".html"
          onChange={e => setFile(e.target.files?.[0] || null)}
          className="mb-4"
        />

        <button
          onClick={handleUpload}
          className="w-full bg-blue-600 hover:bg-blue-700 text-white p-2 rounded"
        >
          Upload
        </button>

        {status && <p className="mt-4 text-white">{status}</p>}
      </div>
    </div>
  );
}